
body {
	background-color: rgba(229, 229, 229, 0.44) !important;
}
#film-info {
	width: 80%;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	width: 100%;
	height: 90%;
}
#top-ban {
	height: 50px;
	background-color: rgba(138, 210, 255, 0.71);
	font-size: 2em;
	color: #22313a;
}
#top-ban-label {
	margin-left: 10px;
}
#map-detail {
	width: 80%;
	height: auto;
	float: right;
	margin-top: 10px;
	padding-right: 10px;
}
#film-info {
	width: 18%;
	float: left;
	margin-left: 10px;
	margin-top: 10px;
}



